.col {
    float: left;
    box-sizing: border-box; //ignored by IE11 when using flex
}

.col-right {
    float: right;
    box-sizing: border-box;
}

.col-1 {
    width: calc(1/12 * 100%);
    max-width: 8.33%;
}

.col-2 {
    width: calc(2/12 * 100%);
    max-width: 16.66%;
}

.col-3 {
    width: calc(3/12 * 100%);
    max-width: 25%;
}

.col-4 {
    width: calc(4/12 * 100%);
    max-width: 33.33%; //Fix for IE11
}

.col-5 {
    width: calc(5/12 * 100%);
    max-width: 41.66%;
}

.col-6 {
    width: calc(6/12 * 100%);
    max-width: 50%;
}

.col-7 {
    width: calc(7/12 * 100%);
    max-width: 58.33%;
}

.col-8 {
    width: calc(8/12 * 100%);
    max-width: 66.66%;
}

.col-9 {
    width: calc(9/12 * 100%);
    max-width: 75%;
}

.col-10 {
    width: calc(10/12 * 100%);
    max-width: 83.33%;
}

.col-11 {
    width: calc(11/12 * 100%);
    max-width: 91.66%;
}

.col-12 {
    width: 100%;
    max-width: 100%;
}


