// The same with .clearfix
.row {
    &:before, &:after {
        display: table;
        content: " ";
    }

    &:after {
        clear: both;
    }
}

//-> https://codepen.io/bootstrapped/details/RrabNe/
.row.row-eq-height {
    display: flex;
    flex-wrap: wrap;

    &>[class*='col-'] {
        display: flex;
        flex-direction: column;
    }

    &:before,
    &:after {
        display: flex;
    }
}